<template>
    <div>
        <div v-for="item in menuData" :key="item.index">
            <el-submenu :index="item.index" v-if="item.children && item.children.length > 0">
                <template slot="title">
                    <i :class="item.icon"></i>
                    <span slot="title">{{ item.title }}</span>
                </template>
                <menuTree :menuData="item.children"></menuTree>
            </el-submenu>

            <el-menu-item :index="item.index" :route="item.url" v-else>
                <i :class="item.icon"></i>
                <span slot="title">{{ item.title }}</span>
            </el-menu-item> 
        </div>
    </div>          
</template>

<script>
    // 子组件递归调用时, 无需引入, 根据name名称直接引用
    export default {
        name: 'menuTree',
        props:{
            menuData: {
                type: Array,
                default: ()=>[]
            }
        },
        methods: {
            handleOpen(key, keyPath) {
                console.log(key, keyPath);
            },
            handleClose(key, keyPath) {
                console.log(key, keyPath);
            }
        }
    }
</script>

