<template>
    <el-menu
        class="nav-menu"
        :default-active="active"
        router
    >   
        <menuTree :menuData="menuData"></menuTree>
    </el-menu>
</template>

<script>
    import menuTree from './menuTree'
    import { menuData } from '@/common/menu'

    export default {
        components: { menuTree },
        data(){
            return {
                menuData,
                active: '', // 默认打开的菜单
                forData: [] // menuData格式化
            }
        },
        created() {
            this.setMenuActive();
        },
        methods: {
            // 打开menu当前的active
            setMenuActive(){
                this.formatterArray(menuData);
                let activeObj = this.forData.find(item => item.url === this.$route.path);
                
                if (activeObj) {
                    this.active = activeObj.index;
                } else {
                    this.active = '1';
                }
            },
            // menuData多维数组转成一维数组
            formatterArray(arr) {
                if (Array.isArray(arr)) {
                    arr.forEach(item => {
                        this.forData.push(item);
                        if (item.children && Array.isArray(item.children)) {
                            this.formatterArray(item.children)
                        }
                    })
                }
            }
        }
    }
</script>

<style lang="less" scoped>   
    .nav-menu{
        background-color: rgb(34, 45, 50);
        /deep/ .el-menu-item-group__title{
            padding: 0 !important;
        }
        /deep/ .el-submenu__title {
            span {
                color: white;
            }
            &:hover{
                background-color: rgb(34, 45, 50);
            }
        }
        /deep/ .el-menu-item {
            background-color: rgb(34, 45, 50);
            span {
                color: white;
            }
            &.is-active span{
                color: #409EFF;
            }
        }
    }
    
</style>